<template>
    <div
        id="navbar-admin"
        class="d-flex justify-end align-center"
        style="background-color: "
    >
        <v-btn icon color="white" class="" :to="{ name: 'Inbox' }">
            <v-icon size="25">mdi-email</v-icon>
        </v-btn>
        <!-- <v-btn icon color="white" class="" :to="{ name: 'Notification' }">
            <v-icon size="25">mdi-bell</v-icon>
        </v-btn> -->
        <v-menu
            bottom
            min-width="300px"
            max-width="300"
            nudge-bottom="10"
            offset-y
            left
            :close-on-content-click="false"
            transition="slide-y-reverse-transition"
            class="mx-2"
        >
            <template v-slot:activator="{ on }">
                <v-btn icon color="white" elevation="0" v-on="on">
                    <v-icon size="25">mdi-bell</v-icon>
                </v-btn>
            </template>
            <v-card>
                <v-list-item-content class="">
                    <div class="px-2">
                        <v-alert
                            v-for="(n, i) in alert"
                            :key="i"
                            v-model="n[i]"
                            close-text="Close Alert"
                            color="deep-purple accent-4"
                            dark
                            dismissible
                            transition="scale-transition"
                        >
                            {{ n.title }}
                        </v-alert>
                    </div>
                    <div class="px-2">
                        <v-divider class="my-3"></v-divider>
                        <div class="d-flex justify-center">
                            <v-btn text :to="{ name: 'Notification' }" v-on="on = false"> show all </v-btn>
                        </div>
                    </div>
                </v-list-item-content>
            </v-card>
        </v-menu>
        <v-menu
            bottom
            min-width="200px"
            nudge-bottom="10"
            offset-y
            transition="slide-y-reverse-transition"
        >
            <template v-slot:activator="{ on }">
                <v-btn color="transparent" elevation="0" v-on="on">
                    <v-icon size="25">mdi-account-circle</v-icon>
                    <span class="white--text ml-2"> Jhon doe </span>
                </v-btn>
            </template>
            <v-card>
                <v-list-item-content class="justify-center">
                    <div class="mx-auto text-center">
                        <v-avatar color="#5e72e4">
                            <span class="white--text headline">
                                {{ user.initials }}
                            </span>
                        </v-avatar>
                        <h3>{{ user.fullName }}</h3>
                        <p class="caption mt-1">
                            {{ user.email }}
                        </p>
                        <v-divider class="my-3"></v-divider>
                        <v-btn depressed text :to="{ name: 'Profile' }">
                            Edit Account
                        </v-btn>
                        <v-divider class="my-3"></v-divider>
                        <v-btn depressed text @click="logout"> Logout </v-btn>
                    </div>
                </v-list-item-content>
            </v-card>
        </v-menu>
    </div>
</template>

<script>
import firebase from "firebase";
export default {
    name: "Navbar",

    data() {
        return {
            alert: [
                {
                    title: "lorem ipsum dolor 1",
                    stt: true,
                },
                {
                    title: "lorem ipsum dolor 2",
                    stt: true,
                },
                {
                    title: "lorem ipsum dolor 3",
                    stt: true,
                }
            ],
            user: {
                initials: "JD",
                fullName: "John Doe",
                email: "john.doe@doe.com",
            },
        };
    },

    methods: {
        logout() {
            firebase
                .auth()
                .signOut()
                .then(() => {
                    this.$router.go({ path: this.$router.path });
                });
        },
    },
};
</script>

<style scoped>
#navbar-admin {
    width: 300px;
}
</style>